import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../components/helpers/field";
import {  getTglSystem, setItem } from "../../../components/helpers/function";
import { Panel } from "../../../components/panel/panel";
import Loading from 'react-fullscreen-loading';

class HeadLihatHutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingDataCustomer: false,
      isLoadingBatal: false,
      isLoadingHutang: false,
      FormModal: "",
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }
  componentDidMount() {
    setItem('trx_pemabayaran_hutang',[])
    setItem('sisa_bayar_hutang',[])
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  render() {
    return (
      <Panel>
        <form
          onSubmit={this.props.handleSubmit}
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
          autoComplete="off"
        >
          <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
            <div className="col-4">
              <div className="form-group">
                <label>&nbsp;</label>
                <button
                  className="btn btn-primary btn-block"
                  disabled={this.props.isloading}
                  type="submit"
                >
                  {this.props.isLoading ? (
                    <>
                      <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                      Mencari
                    </>
                  ) : (
                    "Cari Hutang"
                  )}
                </button>
              </div>
            </div>
          </div>
          {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
            </form>
           
      </Panel>
    );
  }
}

HeadLihatHutang = reduxForm({
  form: "HeadLihatHutang",
  enableReinitialize: true,
})(HeadLihatHutang);
// export default connect(null)(HeadLihatHutang);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLihatHutang);
