import {
  React,
  reduxForm,
  Component,
  connect,
  Field,
  NumberOnly,
} from "../../../components/helpers/library";
import Loading from "react-fullscreen-loading";
import { inputGroup } from "../../../components/helpers/field";

// data_barang_penjualan_tmp

class InputBungaPeremajaan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
    };
  }

  componentDidMount() {
    setTimeout(() => {
      let bunga = document.getElementById("bunga").value;
      // console.log(bunga);
      this.props.change("bunga_lama", bunga);
      document.getElementById("bunga_baru").focus()  
    }, 100);
  }
  render() {
    return (
      <>
        <form
          onSubmit={this.props.handleSubmit}
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
          autoComplete="off"
        >
          <div className="row">
            <div className="col-6">
              <Field
                name="bunga_lama"
                id="bunga_lama"
                component={inputGroup}
                type="text"
                textValue="%"
                readOnly={true}
                label="Bunga Lama"
                placeholder="Bunga Lama"
              />
            </div>
            <div className="col-6">
              <Field
                name="bunga_baru"
                id="bunga_baru"
                component={inputGroup}
                textValue="%"
                normalize={NumberOnly}
                type="text"
                label="Bunga Baru"
                placeholder="Bunga Baru"
              />
            </div>
            <div className="col-12 text-right">
              <button
                className="btn btn-primary"
                disabled={this.props.isLoadingBunga}
                type="submit"
              >
                {this.props.isLoadingBunga ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Kirim
                  </>
                ) : (
                  "Kirim"
                )}
              </button>
            </div>
          </div>
          {this.props.isLoadingBunga ? (
            <Loading
              loading
              background="rgba(0, 0, 0, 0.35)"
              loaderColor="rgba(94, 147, 117, 1)"
            />
          ) : null}
        </form>
      </>
    );
  }
}

InputBungaPeremajaan = reduxForm({
  form: "InputBungaPeremajaan",
  enableReinitialize: true,
})(InputBungaPeremajaan);

export default connect(null)(InputBungaPeremajaan);
