const ValidaasiHakAkses = (value) => {
    const errors = {};
    if (!value.user_id) {
        errors.user_id = "User Id Harus Diisi";
    }
    if (!value.password) {
        errors.password = "Password Harus Diisi";
    }
    if (!value.keterangan) {
        errors.keterangan = "Keterangan Harus Diisi";
    }
    return errors;
}

export default ValidaasiHakAkses;


