import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { HiidenFiled, ReanderField } from "../../../components/helpers/field";
import { getToday } from "../../../components/helpers/function";
import Tabel from "../../../components/helpers/tabel";
import ValidasiServicesSelesai from "../../../Validasi/Services/ValidasiServicesSelesai";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
class PelunasanHutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date("Y-m-d"),
      columns: [
        {
          dataField: "nama_barang",
          text: "Nama Barang",
          footer: "",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(1)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              1
            ),
        },
        {
          dataField: "jumlah",
          text: "Jumlah",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
      ],
    };
  }
  getTotalAll() {
    let hasil =
      JSON.parse(localStorage.getItem("total_rp_service_ambil")) || [];
    let total = 0;
    hasil.forEach((item, index) => {
      total = total + item.jml;
    });
    return parseInt(total).toLocaleString("kr-KO");
  }
  getTotal(rowIndex) {
    // console.log());
    let cek = JSON.parse(localStorage.getItem("total_rp_service_ambil")) || [];
    let hasil = {
      jml: parseInt(
        this.props.detail_barang[rowIndex].ongkos *
          this.props.detail_barang[rowIndex].jumlah
      ),
    };
    cek.push(hasil);
    localStorage.setItem("total_rp_service_ambil", JSON.stringify(cek));
    return parseInt(
      this.props.detail_barang[rowIndex].ongkos *
        this.props.detail_barang[rowIndex].jumlah
    ).toLocaleString("kr-KO");
  }
  hidtungDenda() {
    this.props.change("jumlah_bayar", this.props.jumlah_bayar);
  }

  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <Field
              id="tgl_system"
              name="tgl_system"
              component={HiidenFiled}
              type="hidden"
              readOnly={true}
            />
            <Field
              id="no_faktur_hutang"
              name="no_faktur_hutang"
              component={HiidenFiled}
              type="hidden"
              readOnly={true}
            />
            <Field
              name="nama_customer"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Nama Customer"
              placeholder="Masukan Nama Cistomer"
            />
          </div>
          <div className="col-4">
            <Field
              name="alamat_customer"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Alamat Customer"
              placeholder="Masukan Alamat Cistomer"
            />
          </div>
          <div className="col-4">
            <Field
              name="no_hp"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="No Hp"
              placeholder="Masukan No Hp"
            />
          </div>
          <div className="col-4 d-none">
            <Field
              id="tgl_lunas_hutang"
              onChange={this.hidtungDenda()}
              name="tgl_lunas_hutang"
              component={ReanderField}
              type="date"
              label="Tanggal Lunas Hutang"
            />
          </div>

          <div className="col-4">
            <Field
              id="total_hutang"
              name="total_hutang"
              component={ReanderField}
              type="text"
              label="Hutang Pokok"
              readOnly={true}
            />
          </div>
          <div className="col-4">
            <Field
              id="bunga"
              name="bunga"
              component={ReanderField}
              type="text"
              label="Bunga"
              readOnly={true}
            />
          </div>
          <div className="col-4">
            <Field
              name="lama_pinjam"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Lama Hari"
            />
          </div>
          <div className="col-4">
            <Field
              name="tgl_tempo"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Tanggal Jatuh Tempo"
            />
          </div>
          <div className="col-4">
           
            <Field
              id="jumlah_bayar_pelunasan_hutang"
              name="jumlah_bayar"
              component={ReanderField}
              type="telp"
              readOnly={true}
              {...currencyMask}
              label="Sisa Hutang + Bunga"
            />
          </div>
          <div className="col-2">
            <label> &nbsp;</label>
            <button
              type="button"
              onClick={() => this.props.showModalPembayaran()}
              className="btn btn-success btn-block"
            >
              Pelunasan Hutang
            </button>
          </div>
          <div className="col-2">
            <label> &nbsp;</label>
            <button
              type="button"
              onClick={() => this.props.showModalPeremajaan()}
              className="btn btn-primary btn-block"
            >
              Peremajaan
            </button>
          </div>
          <div className="col-12">
            <Tabel
              keyField="nama_barang"
              exportCsv={false}
              data={this.props.detail_barang}
              columns={this.state.columns}
              pagination="off"
              empty={true}
              textEmpty="Data Barang Kosong"
            />
          </div>
          <div className="col-3">
            <button
              type="button"
              onClick={this.props.back}
              className="btn btn-primary btn-block"
            >
              <i className="fa fa-arrow-left"></i> Kembali
            </button>
          </div>
         
        </div>
        <br />

        {/* <ModalGlobal title="Pembayaran" content={<ModalPembayaran />} /> */}
      </form>
    );
  }
}

PelunasanHutang = reduxForm({
  form: "pelunasanTransaksiHutang",
  enableReinitialize: true,
  validate: ValidasiServicesSelesai,
})(PelunasanHutang);
// export default connect(maptostate, null)(PelunasanHutang);

export default connect((state) => {
  // const selector = formValueSelector("pelunasanTransaksiHutang"); // <-- same as form name
  // let tgl_sekarang = new Date(selector(state, "tgl_lunas_hutang"));
  // let tg_system = new Date(state.hutang.showModalLihatHutang.tgl_system);
  // let hitung_tanggal =
  //   (tgl_sekarang.getTime() - tg_system.getTime()) / (24 * 60 * 60 * 1000);
  // let bunga_rp =
  //   (parseFloat(state.hutang.showModalLihatHutang.bunga || 0) /
  //     parseFloat(100)) *
  //   parseFloat(state.hutang.showModalLihatHutang.jumlah_hutang || 0) *
  //   (parseFloat(hitung_tanggal || 0) / parseFloat(30));
    
    localStorage.setItem('jml_byar_pelunasan_hutang',JSON.stringify(state.hutang.showModalLihatHutang.jumlah_bayar))
  return {
    detail_barang: state.hutang.showModalLihatHutang.detail_barang,
    jumlah_bayar: state.hutang.showModalLihatHutang.jumlah_bayar,
    lama_pinjam: state.hutang.showModalLihatHutang.lama_pinjam,
    initialValues: {
      tgl_lunas_hutang: getToday(),
      tgl_system: state.hutang.showModalLihatHutang.tgl_system,
      no_faktur_hutang: state.hutang.showModalLihatHutang.no_faktur_hutang,
      lama_pinjam: state.hutang.showModalLihatHutang.lama_pinjam,
      tgl_tempo: state.hutang.showModalLihatHutang.tgl_tempo,
      no_hp: state.hutang.showModalLihatHutang.no_hp,
      nama_customer: state.hutang.showModalLihatHutang.nama_customer,
      alamat_customer: state.hutang.showModalLihatHutang.alamat_customer,
      total_hutang: parseInt(
        state.hutang.showModalLihatHutang.jumlah_hutang
      ).toLocaleString("kr-KO"),
      bunga: state.hutang.showModalLihatHutang.bunga,
    },
  };
})(PelunasanHutang);
